export const getStaticProps: GetStaticProps = async ({ params: {slug, postType} }) => {
    // console.trace(postType, slug);
    let filesDirPath = GetBlogPostsDir();

    if(postType === 'code') {
        filesDirPath = GetCodePostsDir();
    }
//   const filesDirPath = GetBlogPostsDir();
  const postFilePath = path.join(filesDirPath, `${slug}.mdx`);
  // console.trace(postFilePath);
  const source = fs.readFileSync(postFilePath, 'utf-8');

  // const { content, data } = matter(source);

  // const mdxSource = await serialize(content, {
  //   // Optionally pass remark/rehype plugins
  //   mdxOptions: {
  //     remarkPlugins: [remarkGfm],
  //     rehypePlugins: [
  //       rehypeSlug,
  //       rehypeCodeTitles,
  //       rehypePrism,
  //       [
  //         rehypeAutolinkHeadings,
  //         {
  //           properties: {
  //             className: ['anchor'],
  //           },
  //         },
  //       ],
  //     ],
  //     format: 'mdx',
  //   },
  //   scope: data,
  // });

  // This plugin is an example to let users write HTML with directives.
  // Itâ€™s informative but rather useless.
  // See below for others examples.
  /** @type {import('unified').Plugin<[], import('mdast').Root>} */
  const myRemarkPlugin = () => {
    return (tree) => {
      visit(tree, (node) => {
        if (
          node.type === 'textDirective' ||
          node.type === 'leafDirective' ||
          node.type === 'containerDirective'
        ) {
          const data = node.data || (node.data = {})
          const hast = h(node.name, node.attributes)

          data.hName = hast.tagName
          data.hProperties = hast.properties
        }
      })
    }
  }

  const { code, frontmatter } = await bundleMDX({
    source: source,
    mdxOptions(options, fm) {
      // this is the recommended way to add custom remark/rehype plugins:
      // The syntax might look weird, but it protects you in case we add/remove
      // plugins in the future.
      options.remarkPlugins = [...(options.remarkPlugins ?? []),
      [remarkGfm],
      [remarkCodeHike, {
        lineNumbers: true,
        showCopyButton: true,
        theme: lightTheme
      }],
      [remarkDirective],
      [myRemarkPlugin]
      ]
      // options.rehypePlugins = [...(options.rehypePlugins ?? []), [rehypeHighlight]]

      return options
    },
  });

  const relatedPostsSlugs = frontmatter['relatedPosts'] || [];
  const relatedPosts = relatedPostsSlugs.map((slug:string) => {
    
    return getPostBySlug(slug, false, ['title', 'description', 'author', 'date', 'image', 'slug']);
  });
  frontmatter['relatedPosts'] = relatedPosts;
  // console.log(frontmatter);

  return {
    props: {
      source: code,
      frontMatter: frontmatter,
    },
  };
};

export const getStaticPaths: GetStaticPaths = async () => {
    const postFiles = GetFilesInDir(GetBlogPostsDir()).filter((path) => /\.mdx?$/.test(path));
    const blogPosts = postFiles
        .map((path) => path.replace(/\.mdx?$/, ''))
        .map((slug) => { return { params: {postType: 'blog', slug: path.parse(slug).name} } });

    const codeFiles = GetFilesInDir(GetCodePostsDir()).filter((path) => /\.mdx?$/.test(path));
    const codePosts = codeFiles
        .map((path) => path.replace(/\.mdx?$/, ''))
        .map((slug) => { return { params: { postType: 'code', slug: path.parse(slug).name} } });
    const paths = blogPosts.concat(codePosts);
    // console.trace(paths);
    return {
        paths,
        fallback: false,
    };
};

export default PostPage;
