import NextHead from 'next/head';
import { useRouter } from 'next/router';
import React from 'react';
import { GetHostedPath } from '../lib/FrontEndUtils';
import { MetaProps } from '../types/layout';

const WEBSITE_HOST_URL = process.env.WEBSITE_HOST_URL || "https://ajaymonga.github.io/general-goods";
// const DOMAIN_PATH = process.env.IS_LOCAL === 'FALSE' ? '/general-goods' : "";
const Head = ({ customMeta }: { customMeta?: MetaProps }): JSX.Element => {
  const router = useRouter();
  const meta: MetaProps = {
    title: "Ajay's General Goods",
    description:
      'Notes on things which I find interesting.',
    image: `${WEBSITE_HOST_URL}/images/site-preview.png`,
    type: 'blog',
    ...customMeta,
  };

  return (
    <NextHead>

      <link rel="apple-touch-icon" sizes="76x76" href={`${GetHostedPath()}/assets/img/favicon.ico`} />
      <link rel="icon" type="image/png" href={`${GetHostedPath()}/assets/img/favicon.ico`} />
      <meta httpEquiv="X-UA-Compatible" content="IE=edge,chrome=1" />
      <meta content='width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0, shrink-to-fit=no' name='viewport' />

      <title>{meta.title}</title>
      <meta content={meta.description} name="description" />
      <meta property="og:url" content={`${WEBSITE_HOST_URL}${router.asPath}`} />
      <link rel="canonical" href={`${WEBSITE_HOST_URL}${router.asPath}`} />
      <meta property="og:type" content={meta.type} />
      <meta property="og:site_name" content={meta.title} />
      <meta property="og:description" content={meta.description} />
      <meta property="og:title" content={meta.title} />
      <meta property="og:image" content={`/${meta.image}`} />
      {/* <meta name="twitter:card" content="summary_large_image" /> */}
      <meta name="twitter:title" content={meta.title} />
      <meta name="twitter:description" content={meta.description} />
      <meta name="twitter:image" content={`/${meta.image}`} />
      {meta.date && (
        <meta property="article:published_time" content={meta.date} />
      )}
    </NextHead>
  );
};


